import { observer } from "mobx-react-lite";
import React, { Fragment, useContext, useState } from "react";
import { RootStoreContext } from "../../stores/rootStore";
import HardwareList from "./HardwareList";
import { RouteComponentProps } from "react-router-dom";
import { useHistory } from "react-router-dom";

interface RouteParams {
  category: string;
}

interface IProps extends RouteComponentProps<RouteParams> {}

const HardwarePage: React.FC<IProps> = ({ match, location }) => {
  const rootStore = useContext(RootStoreContext);
  const [loading, setLoading] = useState(true);
  const { getData, dispose, categories, products } = rootStore.hardwareStore;

  const history = useHistory();

  return (
    <Fragment>
      <div id="goldRibbon" className="fxFixed">
        {categories.find((c) => c.id === match.params.category)?.text}
      </div>
      {match.params.category === "coilheads" && (
        <div className="productFilterWrapper fxFixed fxDisplay fxAlignCenter">
          <div className="productFilter fxWrap fxJustifyCenter fxDisplay fxAlignCenter">
            <span className="productFilterCaption">Select your tank</span>
            <select
              value={
                new URLSearchParams(location.search)
                  .get("fitment")
                  ?.toString() ?? ""
              }
              onChange={(e) => {
                history.push({
                  pathname: `/${match.params.category}`,
                  search:
                    String(e.currentTarget.value) === ""
                      ? ""
                      : `?fitment=${String(e.currentTarget.value)}`,
                });
              }}
              //setFitmentFilter(String(e.currentTarget.value))

              id="productFilterValue"
            >
              <option value="">-- All --</option>
              <option value="spryte">Aspire Aspire K1 BVC</option>
              <option value="breeze">Aspire Breeze / Breeze 2</option>
              <option value="spryte">Aspire Spryte</option>
              <option value="melo">E-Leaf iJust S,2</option>
              <option value="melo">E-Leaf Melo 1-3</option>
              <option value="nrg">E-Leaf Ello</option>
              <option value="nrg">Geekvape Shield / Cerberus</option>
              <option value="nrg">Geekvape Aero/AeroMesh</option>
              <option value="nrg">Geekvape Illusion Mini</option>
              <option value="zseries">Geekvape Zeus</option>
              <option value="pseries">
                GeekVape Aegis Boost Pro Pod / Obelisk 60
              </option>
              <option value="bseries">GeekVape Aegis Boost / Boost Plus</option>
              <option value="bseries">
                GeekVape Hero / Zeus Nano / Z50 Kit
              </option>
              <option value="spryte">iJoy Mercury Resin</option>
              <option value="zenith">
                Innokin Zlide / Zenith / Kroma-Z Pod
              </option>
              <option value="aio">Joyetech EGO AIO</option>
              <option value="aio">Joyetech Cubis</option>
              <option value="aio">Joyetech Cuboid Mini</option>
              <option value="rpm40">SMOK RPM 40</option>
              <option value="rpm80 rpm40">SMOK RPM 80</option>
              <option value="nrg">SMOK TFV8 Baby/BigBaby</option>
              <option value="nrg">SMOK TFV12 Baby Prince</option>
              <option value="tfv8">SMOK TFV8 CloudBeast</option>
              <option value="nrg">Starss Blazer Mesh</option>
              <option value="a2">Uwell Caliburn A2</option>
              <option value="skrr nrg">Vaporesso SKRR</option>
              <option value="nrg">Vaporesso Barr</option>
              <option value="nrg">Vaporesso NRG</option>
              <option value="nrg">Vapor Storm Hawk</option>
              <option value="uforce">VooPoo UForce 1-3</option>
              <option value="pnp">VooPoo Drag S/X / Vinci Pod</option>
            </select>
          </div>
        </div>
      )}

      <HardwareList
        category={match.params.category}
        fitmentFilter={
          match.params.category === "coilheads"
            ? new URLSearchParams(location.search).get("fitment")?.toString()
            : undefined
        }
      />
    </Fragment>
  );
};

export default observer(HardwarePage);
