import { observer } from "mobx-react-lite";
import React, {
  Fragment,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { RootStoreContext } from "../../stores/rootStore";
import { Link } from "react-router-dom";
import { getProductID } from "../../Helpers/helper";
import PageLoader from "../PageLoader";
import { categories, IHardwareItem } from "../../models/models";

interface IProps {
  limitPerCategory: number;
}

const HardwareHotList: React.FC<IProps> = ({ limitPerCategory }) => {
  const rootStore = useContext(RootStoreContext);
  const [loading, setLoading] = useState(true);
  const { getAllData, dispose, products } = rootStore.hardwareStore;

  const init = useCallback(async () => {
    await getAllData().then(() => setLoading(false));
  }, []);

  const trimProducts = () => {
    var res: IHardwareItem[] = [];

    const cats = categories.map((c) => c.id);

    for (const cat of cats) {
      const productsInCategory = products.filter(
        (p) => p.category === cat && p.hot === "TRUE"
      );

      const productCount = productsInCategory.length;

      for (let i = 0; i < Math.min(productCount, limitPerCategory); i++) {
        let x = Math.floor(Math.random() * (productsInCategory.length - 1));

        res.push(productsInCategory.splice(x, 1)[0]);
      }
    }
    return res;
  };

  useEffect(() => {
    init();
  }, []);

  return loading ? (
    <PageLoader />
  ) : (
    <div className="product-parent fxDisplay fxWrap fxJustifyCenter content">
      {trimProducts().map((product, itemIndex, array) => (
        <Fragment key={itemIndex}>
          <Link
            key={itemIndex}
            to={`/${product.category}/${getProductID(product)}`}
            className={`product-item ${product.new ? "new" : ""} ${
              product.hot ? "hot" : ""
            }`}
          >
            <figure>
              <img
                className="product-item-image"
                src={`../images/products/${product.category}/${getProductID(
                  product
                )}.jpg`}
              />
            </figure>
            <label className="product-item-brand">{product.brand}</label>
            <label className="product-item-title">{product.title}</label>
            <span className="product-item-info">{product.info}</span>
            <div className="product-item-buy">
              {product.oldPrice && (
                <div className="product-item-oldprice">
                  {product.oldPrice.toFixed(2)}
                </div>
              )}
              <div className="product-item-price">
                {product.price ? product.price.toFixed(2) : "ERROR"}
              </div>
            </div>
          </Link>
        </Fragment>
      ))}
    </div>
  );
};

export default observer(HardwareHotList);
