import { observer } from "mobx-react-lite";
import React, {
  Fragment,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { RootStoreContext } from "../../stores/rootStore";
import { Link } from "react-router-dom";
import { getProductID } from "../../Helpers/helper";
import PageLoader from "../PageLoader";

interface IProps {
  category: string;
  // hideSubCategory?: boolean;
  fitmentFilter?: string;
  // hotOnly?: boolean;
  // limit?: number;
}

const HardwareList: React.FC<IProps> = ({
  category,
  // hideSubCategory,
  fitmentFilter,
  // hotOnly,
}) => {
  const rootStore = useContext(RootStoreContext);
  const [loading, setLoading] = useState(false);
  const { getData, dispose, products } = rootStore.hardwareStore;

  const init = useCallback(async () => {
    setLoading(true);
    await getData(category);
    setLoading(false);
  }, []);

  useEffect(() => {
    init();
  }, [init]);

  return loading ? (
    <PageLoader />
  ) : (
    <div className="product-parent fxDisplay fxWrap fxJustifyCenter content">
      {products
        .filter((p) => (category ? p.category === category : p))
        // .filter((p) => (hotOnly ? p.hot === "TRUE" : p))
        .filter((p) => (fitmentFilter ? p.fitment === fitmentFilter : p))
        .map((product, itemIndex, array) => (
          <Fragment key={itemIndex}>
            {
              // !hideSubCategory &&
              product.subCat &&
                (itemIndex === 0 ||
                  product.subCat !== array[itemIndex - 1].subCat) && (
                  <span className="product-item-hr">{product.subCat}</span>
                )
            }

            <Link
              key={itemIndex}
              to={`/${product.category}/${getProductID(product)}`}
              className={`product-item ${product.new ? "new" : ""} ${
                product.hot ? "hot" : ""
              }`}
            >
              <figure>
                <img
                  className="product-item-image"
                  src={`../images/products/${product.category}/${getProductID(
                    product
                  )}.jpg`}
                />
              </figure>
              <label className="product-item-brand">{product.brand}</label>
              <label className="product-item-title">{product.title}</label>
              <span className="product-item-info">{product.info}</span>
              <div className="product-item-buy">
                {product.oldPrice && (
                  <div className="product-item-oldprice">
                    {product.oldPrice.toFixed(2)}
                  </div>
                )}
                <div className="product-item-price">
                  {product.price ? product.price.toFixed(2) : "ERROR"}
                </div>
              </div>
            </Link>
          </Fragment>
        ))}
    </div>
  );
};

export default observer(HardwareList);
