import { observer } from "mobx-react-lite";
import React, { Fragment, useContext, useState } from "react";
import { Link } from "react-router-dom";
import "../styles/Header.css";
import { useHistory } from "react-router-dom";

import { RootStoreContext } from "../stores/rootStore";
//import firebase from "firebase";
import { useAuthState } from "react-firebase-hooks/auth";

//import fbConfig from "../firebase";

const Header = () => {
  //const firestore = firebase.firestore();
  // const [user] = useAuthState(fbConfig.auth);
  const [openedMenu, setOpenedMenu] = useState(false);

  const rootStore = useContext(RootStoreContext);
  const { session, logOut, credentialPopup, setCredentialPopup } =
    rootStore.commonStore;

  const history = useHistory();

  return (
    <div id="header" className="fxFixed">
      <div id="headerContainer">
        <Link to="/">
          <img id="mainLogo" alt="Logo" src="../images/mainLogo.png" />
          <img
            id="mainLogoSmall"
            alt="Logo"
            style={{ display: "none" }}
            src="../images/mainlogosmall.png"
          />
        </Link>

        {session && (
          <span id="headerLoginText">Hello&nbsp;{session.name}!</span>
        )}

        <img
          id="headerMenuOpener"
          onClick={() => setOpenedMenu(true)}
          src="../images/menu/menu_opener.svg"
        />
        <div
          id="headerMenuCloser"
          className={openedMenu ? "opened" : ""}
          onClick={() => setOpenedMenu(false)}
        ></div>

        <div id="headerMenu" className={openedMenu ? "opened" : ""}>
          <Link to="/" onClick={() => setOpenedMenu(false)}>
            <img
              className="menuImg"
              alt="Products"
              src="../images/menu/menu_products.svg"
            />
            HOME
          </Link>
          {/* <span id="headerNectar" onClick={() => history.push("/")}>
           
          </span> */}
          {session ? (
            <Fragment>
              <Link to="/mylab" onClick={() => setOpenedMenu(false)}>
                <img
                  className="menuImg"
                  alt="My Lab"
                  src="../images/menu/menu_mylab.svg"
                />
                myLAB
              </Link>
              {/* <span id="headerMyLab" onClick={() => history.push("/mylab")}> 
                
              </span>*/}

              <Link to="/myaccount" onClick={() => setOpenedMenu(false)}>
                <img
                  className="menuImg"
                  alt="My Account"
                  src="../images/menu/menu_myaccount.svg"
                />
                ACCOUNT
              </Link>
              {/* <span
                id="headerMyAccount"
                onClick={() => history.push("/myaccount")}
              >
               
              </span> */}
              {
                //fbConfig.auth.currentUser
                session && (
                  <Link
                    to=""
                    onClick={() => {
                      setOpenedMenu(false);
                      logOut();
                    }}
                  >
                    <img
                      className="menuImg"
                      alt="Logout"
                      src="../images/menu/menu_logout.svg"
                    />
                    LOGOUT
                  </Link>
                  // <span id="headerLogout" onClick={() => logOut()}>

                  // </span>
                )
              }
            </Fragment>
          ) : (
            <Fragment>
              <Link
                to=""
                onClick={() => {
                  setOpenedMenu(false);
                  setCredentialPopup("login");
                }}
              >
                <img
                  className="menuImg"
                  alt="Login"
                  src="../images/menu/menu_login.svg"
                />
                LOGIN
              </Link>
            </Fragment>
            // <span id="headerLogin" onClick={() => setCredentialPopup("login")}>

            // </span>
          )}
        </div>
      </div>
    </div>
  );
};

export default observer(Header);
