import { action, makeObservable, observable, runInAction } from "mobx";
import agent from "../Helpers/agent";
import { getProductID } from "../Helpers/helper";
import { categories, IHardwareItem, IHardwareOrder } from "../models/models";
import { RootStore } from "./rootStore";

export default class hardwareStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    makeObservable(this);
    this.rootStore = rootStore;
  }

  @observable products: IHardwareItem[] = [];
  @observable chosenProduct?: IHardwareItem = undefined;
  @observable ordering: boolean = false;

  categories = [
    { id: "accessories", text: "accessories" },
    { id: "atomizers", text: "atomizers" },
    { id: "batteries", text: "batteries" },
    { id: "chargers", text: "chargers" },
    { id: "coilheads", text: "coilheads" },
    { id: "glasstubes", text: "glasstubes" },
    { id: "mods", text: "mods" },
    { id: "rebuilding", text: "rebuilding" },
    { id: "starterkits", text: "starterkits" },
  ];

  @action getData = async (category: string) => {
    //  const cats = category ? [category] : categories.map((c) => c.id);

    // forEach is not good with Await :D
    // for (const cat of cats) {
    const categoryNotInCache =
      this.products.findIndex((p) => p.category === category) < 0;

    if (categoryNotInCache) {
      var fetchedProducts = await agent.hardware.getLocal(category);

      fetchedProducts = fetchedProducts.map((obj: any) => ({
        ...obj,
        category: category,
      }));

      ///console.info("fetched", fetchedProducts.length, cat);
      runInAction(() => {
        this.products = this.products.concat(fetchedProducts);
      });
    }
    // }
  };

  @action getAllData = async () => {
    const cats = categories.map((c) => c.id);

    // var allProducts: IHardwareItem[] = [];

    // forEach is not good with Await :D
    for (const cat of cats) {
      const categoryNotInCache =
        this.products.findIndex((p) => p.category === cat) < 0;

      if (categoryNotInCache) {
        var fetchedProducts = await agent.hardware.getLocal(cat);

        fetchedProducts = fetchedProducts.map((obj: any) => ({
          ...obj,
          category: cat,
        }));

        // allProducts = allProducts.concat(fetchedProducts);

        runInAction(() => {
          this.products = this.products.concat(fetchedProducts);
        });
      }
    }

    // runInAction(() => {
    //   this.products = allProducts;
    // });
  };

  @action placeOrder = async (order: IHardwareOrder) => {
    const session = this.rootStore.commonStore.session;
    if (session === undefined) return;

    runInAction(() => {
      this.ordering = true;
    });
    const res = await agent.hardware.placeOrder(order, session);

    runInAction(() => {
      this.ordering = false;
    });

    if (res.success === true) {
      const remembeMe = localStorage.getItem("token") !== null;
      if (session && res.points && res.points !== 0)
        this.rootStore.commonStore.createSession(
          { ...session, points: res.points },
          remembeMe
        );
    }
    return res;
  };

  @action dispose = () => {
    this.chosenProduct = undefined;
    // this.products = [];
  };

  @action getProduct = async (category: string, productId: string) => {
    const categoryNotInCache =
      this.products.findIndex((i) => i.category === category) < 0;

    if (categoryNotInCache) await this.getData(category);

    const prod = this.products.find((p) => getProductID(p) === productId);
    return prod;
  };
}
