import { observer } from "mobx-react-lite";
import React, { Fragment, useContext, useEffect } from "react";
import { formatDate } from "../Helpers/helper";
import { RootStoreContext } from "../stores/rootStore";
import ChangePassForm from "./ChangePassForm";
import PageLoader from "./PageLoader";

const MyAccount: React.FC = () => {
  const rootStore = useContext(RootStoreContext);

  const {
    getData,
    dispose,
    myAcc,
    changeMarketingLoading,
    changeMarketing,
    loading,
    changePassFormVisible,
    toggleChangePassPopup,
  } = rootStore.myAccountStore;

  const { session } = rootStore.commonStore;

  useEffect(() => {
    getData();
    return () => {
      dispose();
    };
  }, [getData, dispose, session]);

  return loading ? (
    <PageLoader loadingText="loading" componentText="account" />
  ) : myAcc === undefined ? (
    <div />
  ) : (
    <Fragment>
      {changePassFormVisible && <ChangePassForm />}

      <div id="goldRibbon" className="fxFixed">
        My Account
      </div>

      <div id="myAccountDetails">
        <h2 id="myAccountDetailsTitle">My Details</h2>
        <label>User ID</label>
        <span id="myUserID">{myAcc?.userId}</span>
        <label>Member Since</label>
        <span id="myMembership">{myAcc && formatDate(myAcc.memberFrom)}</span>

        <label>Points</label>
        <span id="myAccountPoints">{myAcc?.points}</span>
        <label>Name and Surname</label>
        <span id="myAccountName">{myAcc?.fullName}</span>
        <label>Email Address</label>
        <span id="myAccountEmail">{myAcc?.email}</span>

        <label>Mobile Number</label>
        <span id="myAccountMobile">{myAcc?.mobile}</span>
        <label>Password</label>

        <a
          onClick={() => {
            toggleChangePassPopup(true);
            window.scrollTo(0, 0);
          }}
        >
          change password
        </a>
        <span
          id="myAccountMarketing"
          className={
            changeMarketingLoading === true ? "" : String(myAcc?.marketing)
          }
        >
          {changeMarketingLoading ? (
            <Fragment>
              Just a moment please, we are processing your request...
            </Fragment>
          ) : (
            <Fragment>
              {myAcc?.marketing === false && (
                <Fragment>
                  You are not subscribed to recieve emails regarding discounts,
                  offers, gifts or new products.
                  <a id="changeMarketing" onClick={() => changeMarketing(true)}>
                    {" "}
                    Click here to Subscribe now!
                  </a>
                </Fragment>
              )}
              {myAcc?.marketing === true && (
                <Fragment>
                  You are subscribed to recieve emails regarding discounts,
                  offers, gifts or new products!
                  <a
                    id="changeMarketing"
                    onClick={() => changeMarketing(false)}
                  >
                    {" "}
                    Click here to Unsubscribe
                  </a>
                </Fragment>
              )}
            </Fragment>
          )}
        </span>
      </div>
    </Fragment>
  );
};

export default observer(MyAccount);
