import { observer } from "mobx-react-lite";
import { Fragment, useCallback, useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { getFlavorID } from "../../Helpers/helper";
import { RootStoreContext } from "../../stores/rootStore";
import PageLoader from "../PageLoader";

const NectarList = () => {
  const rootStore = useContext(RootStoreContext);
  const { tastes, tasteFilter, toggleTaste, getData, products, dispose } =
    rootStore.nectarStore;

  const [loading, setLoading] = useState(false);

  const NectarProps = () => (
    <div className="nectarPropsWrapper">
      <div className="nectarProps fxDisplay fxWrap">
        <div
          className={`nectarProp all ${
            tasteFilter.length === 0 ? "selected" : ""
          }`}
          onClick={() => toggleTaste()}
        >
          <b>All</b>
        </div>
        {tastes.map((p) => (
          <div
            key={p}
            onClick={() => toggleTaste(p)}
            className={`nectarProp ${
              tasteFilter.includes(p) ? "selected" : ""
            }`}
          >
            {p}
          </div>
        ))}
      </div>
    </div>
  );

  const init = useCallback(async () => {
    setLoading(true);
    await getData();
    setLoading(false);
  }, []);

  useEffect(() => {
    init();
    return () => {
      dispose();
    };
  }, [init, dispose]);

  return loading ? (
    <PageLoader />
  ) : (
    <Fragment>
      <div id="goldRibbon" className="fxFixed">
        Nectar Collection
      </div>
      <NectarProps />
      <div
        id="nectarCollection"
        className="fxDisplay fxWrap fxJustifyCenter content"
      >
        {products.map(
          (n, index) =>
            tasteFilter.every((i) => n.props.includes(i)) && (
              <Link
                to={`/nectars/${getFlavorID(n.name)}`}
                key={index}
                className={`nectar ${n.new ? "new" : ""}`}
              >
                <figure>
                  <img
                    alt={n.name}
                    className="nectarFlask"
                    src={`../images/products/nectars/${getFlavorID(
                      n.name
                    )}.png`}
                  />
                </figure>
                {n.hot && <span className="hotSell">{n.hot}</span>}
                <span className="nectarName">{n.name}</span>
                <span className="nectarInfo">{n.desc}</span>
              </Link>
            )
        )}
      </div>
    </Fragment>
  );
};

export default observer(NectarList);
