import { action, makeObservable, observable, runInAction } from "mobx";
import agent from "../Helpers/agent";
import { IMyLabItem } from "../models/models";
import { RootStore } from "./rootStore";

export default class myLabStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    makeObservable(this);

    this.rootStore = rootStore;
  }

  @observable products: IMyLabItem[] = [];
  @observable loading: boolean = false;

  @action getData = async () => {
    if (
      this.rootStore.commonStore.session?.userid &&
      this.rootStore.commonStore.session?.seid &&
      this.products.length === 0
    ) {
      try {
        this.loading = true;

        const response = await agent.account.getMyLab(
          this.rootStore.commonStore.session!.userid,
          this.rootStore.commonStore.session!.seid
        );

        if (response.error !== true) {
          runInAction(() => {
            this.products = response.requests;
          });
        } else if (response.message) {
          throw response.message;
        }
      } catch (f) {
        console.log("MyLab Request Error", f);
      } finally {
        runInAction(() => {
          this.loading = false;
        });
      }
    }
  };

  @action dispose = () => {
    this.products = [];
  };
}
