import React from "react";
import "../styles/Slideshow.css";

interface IProps {
  loadingText?: string;
  componentText?: string;
}

const PageLoader: React.FC<IProps> = ({ loadingText, componentText }) => {
  return (
    <div id="pageLoader" className="fxDisplay fxDirCol fxJustifyCenter">
      {loadingText !== undefined && <span>{loadingText}</span>}{" "}
      {componentText !== undefined && <b>{componentText}</b>}
    </div>
  );
};
export default PageLoader;
