import { action, makeObservable, observable, runInAction } from "mobx";
import { useContext } from "react";
import agent from "../Helpers/agent";
import { getFlavorID } from "../Helpers/helper";
import { IHardwareItem, INectarItem, INectarOrder } from "../models/models";
import { RootStore } from "./rootStore";
import { RootStoreContext } from "../stores/rootStore";
import { Router } from "react-router-dom";
import { __RouterContext } from "react-router";

export default class nectarStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    makeObservable(this);
    this.rootStore = rootStore;
  }
  @observable tasteFilter: string[] = [];
  @observable products: INectarItem[] = [];
  @observable ordering: boolean = false;

  tastes: string[] = [
    "pastry",
    "fruity",
    "spicy",
    "sweet",
    "candy",
    "beverage",
    "creamy",
    "minty",
    "nutty",
    "tobacco",
    "unflavored",
  ];

  prices = [
    { size: 30, price: 6, nicType: "F" },
    { size: 60, price: 10.5, nicType: "F" },
    { size: 120, price: 19.5, nicType: "F" },
    { size: 250, price: 37, nicType: "F" },
    { size: 500, price: 70, nicType: "F" },
    { size: 30, price: 7, nicType: "S" },
    { size: 60, price: 12.5, nicType: "S" },
    { size: 120, price: 23.5, nicType: "S" },
  ];

  @action getData = async () => {
    if (this.products.length === 0) {
      var fetchedProducts = await agent.nectar.getLocal();

      runInAction(() => {
        this.products = fetchedProducts;
      });
    }
  };

  @action placeOrder = async (order: INectarOrder) => {
    const session = this.rootStore.commonStore.session;
    if (session === undefined) return;

    runInAction(() => {
      this.ordering = true;
    });
    const res = await agent.nectar.placeOrder(order, session);

    runInAction(() => {
      this.ordering = false;
    });
    if (res.success === true) {
      const remembeMe = localStorage.getItem("token") !== null;

      if (session && res.points && res.points !== 0)
        this.rootStore.commonStore.createSession(
          { ...session, points: res.points },
          remembeMe
        );
    }
    return res;
  };

  @action toggleTaste = (taste?: string) => {
    if (taste) {
      const index = this.tasteFilter.indexOf(taste);

      if (index >= 0) {
        this.tasteFilter.splice(index, 1);
      } else {
        this.tasteFilter = [...this.tasteFilter, taste];
      }
    } else this.tasteFilter = [];
  };

  @action dispose = () => {
    // this.products = [];
    this.tasteFilter = [];
  };

  @action chooseProduct = async (product: IHardwareItem) => {
    // this.chosenProduct = product;
  };

  @action getProduct = async (productId: string) => {
    await this.getData();

    const prod = this.products.find((p) => getFlavorID(p.name) === productId);
    return prod;
  };
}
