import { observer } from "mobx-react-lite";
import React, { Fragment, useContext, useEffect } from "react";
import "../styles/myLab.css";
import { getFlavorID, getProductID } from "../Helpers/helper";
import { IMyLabItem } from "../models/models";
import { RootStoreContext } from "../stores/rootStore";
import PageLoader from "./PageLoader";

const MyLab: React.FC = () => {
  const rootStore = useContext(RootStoreContext);
  const { getData, products, dispose, loading } = rootStore.myLabStore;
  const { session, getStatus, getTypes } = rootStore.commonStore;

  const getMyLabProductId = (item: IMyLabItem) => {
    const id =
      item.flavor.replace(/[^a-z0-9]/gi, "") +
      (item.size ? "_" + item.size.replace(/[^a-z0-9]/gi, "") : "");

    return id;
  };
  useEffect(() => {
    getData();
    return () => {
      dispose();
    };
  }, [getData, dispose, session]);

  var total = 0;
  var totalPoints = 0;

  return (
    <Fragment>
      <div id="goldRibbon" className="fxFixed">
        My Lab
      </div>
      {loading ? (
        <PageLoader loadingText="loading" componentText="myLab" />
      ) : (
        products.length > 0 && (
          <div id="myLabRequestContainer">
            <table className="myLabRequestGrid ">
              <tbody>
                {products?.map((product, index) => {
                  if (!["P"].includes(product.state)) total += product.price;

                  if ((product.points as any) === "") product.points = 0;

                  console.info(product.points, product.points ?? 0);

                  if (product.points > 0) totalPoints += product.points;

                  return product.type === "nectars" ? (
                    <tr key={index} className="myLabRequestRow">
                      <td>
                        <img
                          src={`../images/products/nectars/${getFlavorID(
                            product.flavor
                          )}.png`}
                        />
                      </td>
                      <td className="myLabRequestMidCol">
                        <span className="myLabRequestRowFlavor">
                          {product.flavor}
                        </span>
                        <span className="myLabRequestRowInfo">
                          <span>{`${product.quantity}x ${product.size}`}</span>
                          <span>
                            {product.nicotine === 0
                              ? "Zero-Nicotine"
                              : `${product.nicotine}mg ${
                                  product.nicType === "F" ? "Freebase" : "Salt"
                                }`}
                          </span>
                          <span>
                            {product.vg === 100
                              ? "MAX-VG"
                              : `${product.vg}/${100 - product.vg}`}
                          </span>
                          <span>{product.vaper}</span>
                          <span>{product.datetime}</span>
                        </span>
                      </td>
                      <td className="myLabRequestEndCol">
                        <span className="myLabRequestRowPrice">
                          {product.price.toFixed(2)}
                        </span>
                        {product.points !== 0 && (
                          <span
                            className={`myLabRequestRowPoints ${
                              product.points > 0 ? "positive" : "negative"
                            }`}
                          >
                            {product.points > 0
                              ? `+${product.points} points!`
                              : `${product.points} points`}
                          </span>
                        )}
                        <div
                          className={`myLabRequestGridState state${product.state}`}
                        >
                          {getStatus(true, product.state)}
                        </div>
                      </td>
                    </tr>
                  ) : (
                    <tr key={index} className="myLabRequestRow">
                      <td>
                        <img
                          src={`../images/products/${
                            product.type
                          }/${getMyLabProductId(product)}.jpg`}
                        />
                      </td>
                      <td className="myLabRequestMidCol">
                        <span className="myLabRequestRowFlavor">
                          {product.flavor}
                        </span>
                        <span className="myLabRequestRowInfo">
                          <span>{`${product.quantity}x ${product.size}`}</span>
                          <span>
                            {getTypes(product.type, product.quantity === 1)}
                          </span>

                          <span>{product.datetime}</span>
                        </span>
                      </td>
                      <td className="myLabRequestEndCol">
                        <span className="myLabRequestRowPrice">
                          {product.price.toFixed(2)}
                        </span>
                        {product.points !== 0 && (
                          <span
                            className={`myLabRequestRowPoints ${
                              product.points > 0 ? "positive" : "negative"
                            }`}
                          >
                            {product.points > 0
                              ? `+${product.points} points!`
                              : `${product.points} points`}
                          </span>
                        )}
                        <div
                          className={`myLabRequestGridState state${product.state}`}
                        >
                          {getStatus(false, product.state)}
                        </div>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>

            <div className="myLabRequestTotalSummary">
              <span className="myLabRequestTotalSummaryCost">
                Your order total is <b>{total.toFixed(2)}</b>
              </span>
              <br />
              <span className="myLabRequestTotalSummaryPoints">
                You'll earn <b>{totalPoints} Points</b> once your requests are
                paid and processed!
                <br />
                ...that's your pocket equivalent to{" "}
                <b>{(totalPoints / 100).toFixed(2)}!</b>
              </span>
            </div>
          </div>
        )
      )}
    </Fragment>
  );
};

export default observer(MyLab);
