import { action, makeObservable, observable, runInAction } from "mobx";
import { stringifyKey } from "mobx/dist/internal";
import agent from "../Helpers/agent";
import { ISessionObj } from "../models/models";
import { RootStore } from "./rootStore";

// interface iSession {
//   name: string
// }

export default class CommonStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    makeObservable(this);
    this.rootStore = rootStore;
  }

  getStatus = (isNectar: boolean, state: string) => {
    switch (state) {
      case "":
        return isNectar ? "In-Production" : "Ordered";
      case "R":
        return isNectar ? "Ready!" : "Confirmed";
      case "N":
        return isNectar ? "Ready!" : "Confirmed";
      case "P":
        return "Paid";
      case "PP":
        return "Paid";
      case "PD":
        return "Paid";
      case "U":
        return "Unpaid";
      case "B":
        return "Backordered";
      default:
        return "";
    }
  };

  getTypes = (type: string, singular?: boolean) => {
    switch (type) {
      case "coilheads":
        return singular ? "Coil Head" : "Coil Heads";
      case "starterkits":
        return singular ? "Starter Kit" : "Starter Kits";
      case "nectars":
        return singular ? "Nectar" : "Nectars";
      case "glasstubes":
        return singular ? "Glass Tube" : "Glass Tubes";
      case "rebuilding":
        return singular ? "Rebuilding" : "Rebuilding";
      case "batteries":
        return singular ? "Battery" : "Batteries";
      case "chargers":
        return singular ? "Charger" : "Chargers";
      case "accessories":
        return singular ? "Accessory" : "Accessoriess";
      case "atomizers":
        return singular ? "Atomizer" : "Atomizers";
      case "mods":
        return singular ? "Mod" : "Mods";
      default:
        return "";
    }
  };

  @observable credentialPopup: "login" | "register" | undefined = undefined;

  @observable promptAge: boolean = false;

  @observable systemMessage:
    | { title: string; text: string; css: string; newLocation?: string }
    | undefined = undefined;

  @observable session: ISessionObj | undefined = undefined;

  @action showSystemMessage = (
    title: string,
    text: string,
    css: string,
    newLocation?: string
  ) => {
    this.systemMessage = {
      title: title,
      text: text,
      css: css,
      newLocation: newLocation,
    };

    setTimeout(() => {
      this.systemMessage = undefined;
    }, 5000);
  };

  @action promptAgeAccept = () => {
    this.promptAge = false;
    sessionStorage.setItem("over18", "true");
  };
  @action refreshToken = async () => {
    var rememberMe = true;

    var loginObj: { userId?: string; seId?: string } = {};

    //legacy support
    if (
      localStorage.getItem("userid") !== null &&
      localStorage.getItem("seid") !== null
    ) {
      loginObj.userId = String(localStorage.getItem("userid"));
      loginObj.seId = String(localStorage.getItem("seid"));
      localStorage.removeItem("userid");
      localStorage.removeItem("seid");
      //create session from legacy to support loading mylab onstart
      this.session = {
        userid: loginObj.userId,
        seid: loginObj.seId,
        name: "",
        email: "",
        points: 0,
      };
    }

    //token in local storage
    else if (localStorage.getItem("token") !== null) {
      const token: ISessionObj = JSON.parse(
        atob(String(localStorage.getItem("token")))
      );

      loginObj.userId = token.userid;
      loginObj.seId = token.seid;
    }

    //token in session storage
    else if (sessionStorage.getItem("token") !== null) {
      rememberMe = false;
      const token: ISessionObj = JSON.parse(
        atob(String(sessionStorage.getItem("token")))
      );

      loginObj.userId = token.userid;
      loginObj.seId = token.seid;
    } else {
      this.session = undefined;
      console.info("token removed");
      if (sessionStorage.getItem("over18") === null) this.promptAge = true;
      return;
    }

    if (loginObj.userId && loginObj.seId) {
      console.info("Auto Login - Fetching Token", {
        userId: loginObj.userId,
        sessionId: loginObj.seId,
      });

      const res = await agent.account.refreshToken(
        loginObj.userId,
        loginObj.seId
      );
      this.createSession(res, rememberMe);
    }
  };

  @action setCredentialPopup = (type?: "login" | "register") => {
    this.credentialPopup = type;
  };

  @action logOut = () => {
    this.session = undefined;
    console.info("token removed");

    localStorage.removeItem("token");
    sessionStorage.removeItem("token");
  };

  @action createSession = (obj: ISessionObj, rememberMe: boolean) => {
    if (obj.error !== undefined) {
      this.session = undefined;
      console.info("token removed");

      return;
    }
    const token = btoa(JSON.stringify(obj));
    sessionStorage.setItem("token", token);

    rememberMe && localStorage.setItem("token", token);

    this.session = obj;
    // this.authorizeCallback &&
    //   this.authorizeCallback() &&
    //   this.clearAuthorizeCallback();
  };

  // @action authorize = (callback: any) => {
  //   this.session ? callback() : (this.authorizeCallback = callback);
  // };

  // @action clearAuthorizeCallback = () => {
  //   this.authorizeCallback = undefined;
  // };
}
