import React, { useContext } from "react";
import { useHistory } from "react-router-dom";
import agent from "../Helpers/agent";
import { RootStoreContext } from "../stores/rootStore";
import "../styles/Slideshow.css";

const ChangePassForm: React.FC = () => {
  const rootStore = useContext(RootStoreContext);
  const { session, logOut } = rootStore.commonStore;

  const { toggleChangePassPopup } = rootStore.myAccountStore;

  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState("");
  const [succesful, setSuccesful] = React.useState(false);

  const [formData, updateFormData] = React.useState({
    pass: "",
    newpass: "",
    confirmpass: "",
  });

  const history = useHistory();

  const redirect = () => {
    logOut();
    toggleChangePassPopup(false);
    history.push("/");
  };
  const handleSubmit = async (e: any) => {
    e.preventDefault();
    if (formData.newpass !== formData.confirmpass) {
      setError("Passwords do not match");
    } else
      try {
        setError("");

        if (session?.email) {
          setLoading(true);

          const res = await agent.account.changePass(
            session.email,
            formData.pass,
            formData.newpass
          );

          if (res?.success) {
            setSuccesful(true);
          } else if (res?.error) {
            throw res.error;
          }
        }
      } catch (f) {
        setError("Error. Please try again later.");
        console.log(f);
      } finally {
        setLoading(false);
      }
  };

  return (
    <div
      id="changePassPopupShadow"
      className={`credPopupShadow fxDisplay fxAlignCenter fxJustifyCenter ${
        loading ? "loading" : ""
      }`}
      onClick={() => {
        if (!loading && !succesful) toggleChangePassPopup(false);
      }}
    >
      <style>{`body{overflow: hidden;max-height: 100vh;}`}</style>
      {succesful ? (
        <div id="infoPopupElement" onClick={(e) => e.stopPropagation()}>
          <h1 style={{ color: "#4ab74a" }}>Password changed</h1>
          <h2>Please login again using your new password.</h2>

          <input
            type="button"
            onClick={() => redirect()}
            id="infoPopupOkBtn"
            value="OK"
          />
        </div>
      ) : (
        <div
          className="credPopupForm fxDisplay"
          onClick={(e) => e.stopPropagation()}
        >
          <form className="credPopupData changepass" onSubmit={handleSubmit}>
            <img className="credPopupMainIcon" src="../images/access.png" />
            <span id="changePassPopupTitle" className="credPopupTitle">
              {loading ? "Please wait..." : "Change Password"}
            </span>
            <input
              type="text"
              name="email"
              autoComplete="username email"
              style={{ display: "none" }}
            />
            <table className="credPopupTable">
              <tbody>
                <tr>
                  <td>Current</td>
                  <td align="right">
                    <input
                      required
                      autoComplete="current-password"
                      placeholder="current password"
                      type="password"
                      id="changePassCurrent"
                      value={formData.pass}
                      onChange={(e) =>
                        updateFormData({ ...formData, pass: e.target.value })
                      }
                    />
                  </td>
                </tr>
                <tr>
                  <td>New</td>
                  <td align="right">
                    <input
                      required
                      autoComplete="new-password"
                      placeholder="new password"
                      type="password"
                      id="changePassNew"
                      value={formData.newpass}
                      onChange={(e) =>
                        updateFormData({ ...formData, newpass: e.target.value })
                      }
                    />
                  </td>
                </tr>
                <tr>
                  <td>Confirm</td>
                  <td align="right">
                    <input
                      required
                      autoComplete="new-password"
                      placeholder="confirm password"
                      type="password"
                      id="changePassConfirm"
                      value={formData.confirmpass}
                      onChange={(e) =>
                        updateFormData({
                          ...formData,
                          confirmpass: e.target.value,
                        })
                      }
                    />
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <span id="changePassPopupError" className="credPopupError">
                      {error}
                    </span>
                  </td>
                </tr>
              </tbody>
            </table>

            <input
              type="submit"
              className="credPopupButton proceedCredPopupButton"
              value="Proceed"
            />
            <input
              type="button"
              className="credPopupButton"
              onClick={() => toggleChangePassPopup(false)}
              value="Cancel"
            />
          </form>
        </div>
      )}
    </div>
  );
};
export default ChangePassForm;
