import React, { useContext } from "react";
import AliceCarousel from "react-alice-carousel";
import { Link, useHistory } from "react-router-dom";
import { RootStoreContext } from "../stores/rootStore";
import "../styles/Slideshow.css";

const AgeCheck: React.FC = () => {
  const rootStore = useContext(RootStoreContext);
  const { refreshToken, credentialPopup, session, promptAgeAccept } =
    rootStore.commonStore;

  const history = useHistory();

  return (
    <div id="ageCheck" className="fxDisplay fxJustifyCenter fxAlignCenter">
      <div id="ageCheckWindow">
        <span>Are you at least 18 years old?</span>
        <button
          onClick={() => promptAgeAccept()}
          style={{ background: "yellowgreen" }}
        >
          Yes
        </button>
        <button
          onClick={() => (window.location.href = "http://www.google.com")}
        >
          No
        </button>
      </div>
    </div>
  );
};
export default AgeCheck;
