import React, { useContext } from "react";
import { RootStoreContext } from "../../stores/rootStore";
import agent from "../../Helpers/agent";
import "../../styles/LoginRegister.css";

//import fb from "../../firebase";

const RegisterForm: React.FC = () => {
  const rootStore = useContext(RootStoreContext);
  const { setCredentialPopup } = rootStore.commonStore;

  const [isReferred, setIsReferred] = React.useState(false);
  const [succesful, setSuccesful] = React.useState(false);
  const [formData, updateFormData] = React.useState({
    name: "",
    surname: "",
    email: "",
    mobile: "",
    friendId: "",
  });
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<string | undefined>(undefined);

  const handleSubmit = async (e: any) => {
    e.preventDefault();

    try {
      setLoading(true);
      //fb.register(formData.email, formData.password);

      const res = await agent.account.register(formData);

      if (res?.success) setSuccesful(true);
      else if (res?.error) {
        throw res.error;
      }
    } catch (f) {
      setError("Error. Please try again later.");
      console.log(f);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div
      className={`credPopupShadow fxDisplay fxAlignCenter fxJustifyCenter ${
        loading ? "loading" : ""
      }`}
      onClick={(e) => {
        !loading && setCredentialPopup(undefined);
      }}
    >
      {succesful ? (
        <div id="infoPopupElement" onClick={(e) => e.stopPropagation()}>
          <h1 style={{ color: "#4ab74a" }}>Registration Received!</h1>
          <h2>Your new account is awaiting verification...</h2>
          <p>
            We'll send your login details as soon as we approve your submitted
            details.
          </p>
          <h3>
            See you soon!
            <br />
            <b>The Mama's Nectar Team.</b>
          </h3>

          <input
            type="button"
            onClick={() => setCredentialPopup(undefined)}
            id="infoPopupOkBtn"
            value="OK"
          />
        </div>
      ) : (
        <div
          className="credPopupForm fxDisplay"
          onClick={(e) => e.stopPropagation()}
        >
          <form className="credPopupData" onSubmit={handleSubmit}>
            <img className="credPopupMainIcon" src="../images/register.png" />
            <span className="credPopupTitle">
              {loading ? "Sending Request..." : "Plug with us!"}
            </span>

            <table className="credPopupTable">
              <tbody>
                <tr>
                  <td>Name</td>
                  <td align="right">
                    <input
                      autoComplete="given-name"
                      placeholder="name"
                      name="name"
                      type="text"
                      value={formData.name}
                      onChange={(e) =>
                        updateFormData({ ...formData, name: e.target.value })
                      }
                      required
                    />
                  </td>
                </tr>
                <tr>
                  <td>Surname</td>
                  <td align="right">
                    <input
                      autoComplete="family-name"
                      placeholder="surname"
                      name="surname"
                      type="text"
                      value={formData.surname}
                      onChange={(e) =>
                        updateFormData({ ...formData, surname: e.target.value })
                      }
                      required
                    />
                  </td>
                </tr>
                <tr>
                  <td>E-Mail</td>
                  <td align="right">
                    <input
                      autoComplete="email"
                      placeholder="email"
                      name="username"
                      type="email"
                      value={formData.email}
                      onChange={(e) =>
                        updateFormData({ ...formData, email: e.target.value })
                      }
                      required
                    />
                  </td>
                </tr>
                <tr>
                  <td>Mobile No</td>
                  <td align="right">
                    <input
                      autoComplete="tel"
                      placeholder="mobile"
                      name="mobile"
                      type="text"
                      value={formData.mobile}
                      onChange={(e) =>
                        updateFormData({ ...formData, mobile: e.target.value })
                      }
                      required
                    />
                  </td>
                </tr>

                <tr>
                  <td colSpan={2} align="right">
                    <input
                      type="checkbox"
                      name="registerPopupRecommended"
                      style={{ display: "none" }}
                      id="registerPopupRecommended"
                      checked={isReferred}
                      onChange={(e) => {
                        updateFormData({ ...formData, friendId: "" });
                        setIsReferred(e.target.checked);
                      }}
                    />
                    <label
                      id="loginRecommendedCheckBox"
                      htmlFor="registerPopupRecommended"
                    ></label>
                    <span style={{ fontSize: "14px" }}>
                      Were you recommended by a friend?
                    </span>
                  </td>
                </tr>

                {isReferred && (
                  <tr>
                    <td>Friend</td>
                    <td align="right">
                      <input
                        autoComplete={"no"}
                        placeholder="name & surname"
                        type="text"
                        value={formData.friendId}
                        onChange={(e) =>
                          updateFormData({
                            ...formData,
                            friendId: e.target.value,
                          })
                        }
                        required
                      />
                    </td>
                  </tr>
                )}

                <tr>
                  <td colSpan={2}>
                    <span className="credPopupError">{error}</span>
                  </td>
                </tr>
              </tbody>
            </table>

            <input
              type="submit"
              className="credPopupButton proceedCredPopupButton"
              value="Proceed"
            />
            <input
              type="button"
              onClick={() => setCredentialPopup(undefined)}
              className="credPopupButton"
              value="Cancel"
            />

            <span className="credPopupHowLogin">
              Already have an account?&nbsp;
              <span onClick={() => setCredentialPopup("login")}>
                Login here!
              </span>
            </span>
          </form>
        </div>
      )}
    </div>
  );
};

export default RegisterForm;
