import React, { useContext } from "react";
import { RootStoreContext } from "../../stores/rootStore";
import agent from "../../Helpers/agent";
import "../../styles/LoginRegister.css";

//import fb from "../../firebase";
const LoginForm: React.FC = () => {
  const rootStore = useContext(RootStoreContext);
  const { createSession, setCredentialPopup } = rootStore.commonStore;

  const [rememberMe, setRememberMe] = React.useState(true);
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<string | undefined>(undefined);
  const [formData, updateFormData] = React.useState({
    email: "",
    pass: "",
  });

  const handleSubmit = async (e: any) => {
    e.preventDefault();

    try {
      setLoading(true);
      //fb.signInWithEmail(formData.email, formData.password);

      const res = await agent.account.login(formData.email, formData.pass);

      if (res?.success) {
        createSession(res, rememberMe);
        setCredentialPopup(undefined);
      } else if (res?.error) setError(res.error);
    } catch (f) {
      setError("Error. Please try again later.");
      console.log(f);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div
      className={`credPopupShadow fxDisplay fxAlignCenter fxJustifyCenter ${
        loading ? "loading" : ""
      }`}
      onClick={(e) => {
        !loading && setCredentialPopup(undefined);
      }}
    >
      <div
        className="credPopupForm fxDisplay"
        onClick={(e) => e.stopPropagation()}
      >
        <form
          autoComplete="on"
          className="credPopupData"
          onSubmit={handleSubmit}
        >
          <img className="credPopupMainIcon" src="../images/access.png" />
          <span className="credPopupTitle">
            {loading ? "Authorizing..." : "Account Login"}
          </span>

          <table className="credPopupTable">
            <tbody>
              <tr>
                <td>E-Mail</td>
                <td align="right">
                  <input
                    autoComplete="email"
                    placeholder="email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={(e) =>
                      updateFormData({ ...formData, email: e.target.value })
                    }
                    required
                  />
                </td>
              </tr>
              <tr>
                <td>Password</td>
                <td align="right">
                  <input
                    autoComplete="current-password"
                    placeholder="password"
                    name="pass"
                    type="password"
                    value={formData.pass}
                    onChange={(e) =>
                      updateFormData({ ...formData, pass: e.target.value })
                    }
                    required
                  />
                </td>
              </tr>
              <tr>
                <td></td>
                <td align="right">
                  <input
                    type="checkbox"
                    name="rememberMe"
                    style={{ display: "none" }}
                    onChange={(e) => setRememberMe(e.target.checked)}
                    id="rememberMe"
                    checked={rememberMe}
                  />
                  <label
                    id="loginRememberCheckBox"
                    htmlFor="rememberMe"
                  ></label>
                  <span>remember me</span>
                </td>
              </tr>
              <tr>
                <td colSpan={2}>
                  <span className="credPopupError">{error}</span>
                </td>
              </tr>
            </tbody>
          </table>

          <input
            type="submit"
            className="credPopupButton proceedCredPopupButton"
            value="Proceed"
          />
          <input
            type="button"
            onClick={() => setCredentialPopup()}
            className="credPopupButton"
            value="Cancel"
          />

          <span className="credPopupHowLogin">
            Don't have an account?&nbsp;
            <span onClick={() => setCredentialPopup("register")}>
              Register Now!
            </span>
          </span>
        </form>
      </div>
    </div>
  );
};

export default LoginForm;
