import {
  IHardwareItem,
  IHardwareOrder,
  INectarOrder,
  IRegisterObj,
  ISessionObj,
} from "../models/models";

const _requests = {
  get: (url: string) =>
    fetch(url, {
      // headers: {
      //   "Content-Type": "application/json; charset=utf-8",
      //   "Access-Control-Allow-Origin": "*",
      //   referer: "script.google.com",
      // },
    })
      .then((response) => response.json())
      .then((data) => data)
      .catch((err) => {
        console.log("error:" + err);
      }),
  post: (url: string, data: any) =>
    fetch(url, {
      method: "POST",
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((data) => data)
      .catch((err) => {
        console.log("error:" + err);
      }),
};

const account = {
  login: (email: string, pass: string) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycby_Ylf8PzNJkPZW6GBX9bwOU8afXah-kIdCO_AHZNc40XyO715YGv00sv4xRcKsFnHU/exec?email=${email}&pass=${pass}`
    ),
  refreshToken: (userId: string, seId: string) => {
    return _requests.get(
      `https://script.google.com/macros/s/AKfycby_Ylf8PzNJkPZW6GBX9bwOU8afXah-kIdCO_AHZNc40XyO715YGv00sv4xRcKsFnHU/exec?userid=${userId}&seid=${seId}`
    );
  },
  register: (regObj: IRegisterObj) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbxWNDURdp6d1ttSV8bkrKh5WxSP5OG-0EGt8__VN6sxqguLtKiEFMmL92JyD346Ak_X/exec?${new URLSearchParams(
        { ...regObj }
      ).toString()}`
    ),
  getMyLab: (userId: string, seId: string) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbwib5WDAsDOsuGsivmBVZCkNRj8C8fG5ZBjdnBxx5h29s93x-qcqD1nHeMvmp0mqH-ZfQ/exec?userid=${userId}&seid=${seId}`
    ),

  getMyAccount: (userId: string, seId: string) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbw92Op4tjh__s6WpXcVtvVd_1txV-v9p_0_kepbiqwV26W6vAOqz_9HmUobmSRWyHEVxw/exec?userid=${userId}&seid=${seId}`
    ),

  setMarketing: (email: string, marketing: boolean) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbwN7agPsyNae74lUNFgQBzh70gLViAviV-ZWqNV7PV6Dne2rUWe9yJLUL5Gy7rv0nGZ/exec?email=${email}&marketing=${marketing}`
    ),
  changePass: (email: string, pass: string, newpass: string) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbz4o-4275lbcvLCX91_BENBSRhwGz0Ryj-6mFcxs9V6OXeZfDXLKdWCBu3nBEpZibsomQ/exec?email=${email}&pass=${pass}&newpass=${newpass}`
    ),
};

const hardware = {
  getLocal: (category: string) => _requests.get(`../data/${category}.json`),

  placeOrder: (order: IHardwareOrder, session: ISessionObj) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbxM1_oWrSKfLQjS-NJTsWrPinhq1aXrg4PnlqK4Eu_EjsaFhB_JeOaAZJzS20Vj-_IQ/exec?${new URLSearchParams(
        { ...(order as any), userid: session.userid, seid: session.seid }
      )}`
    ),
};

const nectar = {
  getLocal: () => _requests.get(`../data/nectars.json`),

  placeOrder: (order: INectarOrder, session: ISessionObj) =>
    _requests.get(
      `https://script.google.com/macros/s/AKfycbxmlfDcmN1W9JXHh1z3QhMUYtYwh5OcKH1p5TQOodeHW30xsE8mLDOwNX5QIhywiq3R/exec?${new URLSearchParams(
        { ...(order as any), userid: session.userid, seid: session.seid }
      )}`
    ),
};

const exp = { hardware, nectar, account };
export default exp;
