import React, { Fragment, useContext, useEffect } from "react";
import {
  Route,
  RouteComponentProps,
  Switch,
  withRouter,
} from "react-router-dom";

import "../styles/global.css";
import "../styles/App.css";

//components
import Header from "./Header";

//pages
import Home from "./Home";
import NectarList from "./nectar/NectarList";

import { observer } from "mobx-react-lite";
import { RootStoreContext } from "../stores/rootStore";
import LoginForm from "./auth/LoginForm";
import RegisterForm from "./auth/RegisterForm";

import MyLab from "./MyLab";
import HardwareDetail from "./hardware/HardwareDetail";
import HardwareList from "./hardware/HardwareList";
import NectarDetail from "./nectar/NectarDetail";
import ScrollToTop from "./ScrollToTop";
import HardwarePage from "./hardware/HardwarePage";
import MyAccount from "./MyAccount";
import AgeCheck from "./AgeCheck";
import { createElement } from "react";
import SystemMessage from "./SystemMessage";

const App: React.FC<RouteComponentProps> = () => {
  // console.log(window.innerWidth); use this to dynamically disable or enable mobile/desktop styles
  const rootStore = useContext(RootStoreContext);
  const { refreshToken, credentialPopup, session, promptAge, systemMessage } =
    rootStore.commonStore;

  useEffect(() => {
    console.info("token", session);

    if (!session) {
      refreshToken();
    }

    // if (
    //   sessionStorage.getItem("over18") === null &&
    //   sessionStorage.getItem("userid") == null
    // ) {
    //   $("#ageCheck").show();
    // }

    console.info("App useEffect");
  }, []);

  return (
    <Fragment>
      <Header />
      <SystemMessage />
      <Switch>
        <Route exact path="/myaccount" component={MyAccount} />
        <Route exact path="/mylab" component={MyLab} />
        <Route
          exact
          path="/:category"
          render={(props) => {
            const cat = props.match.params.category;
            if (cat === "nectars") return <NectarList />;

            if (
              rootStore.hardwareStore.categories.findIndex(
                (c) => c.id === cat
              ) >= 0
            )
              return <HardwarePage {...props} />;

            // return <HardwarePage category={props.match.params.category} />;
          }}
        />
        <Route
          exact
          path="/:category/:id"
          render={(props) => {
            const cat = props.match.params.category;
            if (cat === "nectars") return <NectarDetail {...props} />;

            if (
              rootStore.hardwareStore.categories.findIndex(
                (c) => c.id === cat
              ) >= 0
            )
              return <HardwareDetail {...props} />;
          }}
        />
        <Route
          render={() => {
            window.scrollTo(0, 0);
            return (
              <Fragment>
                {/* <ScrollToTop /> */}
                <Home />
              </Fragment>
            );
          }}
        />
      </Switch>

      {promptAge && <AgeCheck />}

      {credentialPopup && (
        <style>{`
        body{
          overflow: hidden;
          max-height: 100vh;
        }

      `}</style>
      )}

      {credentialPopup === "login" && <LoginForm />}
      {credentialPopup === "register" && <RegisterForm />}
    </Fragment>
  );
};

export default withRouter(observer(App));
