import { observer } from "mobx-react-lite";
import React, {
  Fragment,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { RouteComponentProps } from "react-router";
import { getProductID } from "../../Helpers/helper";
import { RootStoreContext } from "../../stores/rootStore";
import { useHistory } from "react-router-dom";
import { IHardwareItem } from "../../models/models";
import PageLoader from "../PageLoader";

interface RouteParams {
  id: string;
  category: string;
}

interface Iprops extends RouteComponentProps<RouteParams> {}

const HardwareDetail: React.FC<Iprops> = ({ match }) => {
  const rootStore = useContext(RootStoreContext);
  const { session, setCredentialPopup, showSystemMessage } =
    rootStore.commonStore;
  const { getProduct, placeOrder, ordering } = rootStore.hardwareStore;

  let history = useHistory();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const [item, setItem] = useState<IHardwareItem | undefined>();

  interface IFormData {
    qty: number;
    usePoints: boolean;
    color?: string;
  }

  const [formData, updateFormData] = React.useState<IFormData>({
    qty: 1,
    usePoints: false,
  });

  // const [qty, setQty] = useState(1);

  const init = useCallback(async () => {
    setLoading(true);
    const res = await getProduct(match.params.category, match.params.id);
    setItem(res);
    setLoading(false);
  }, []);

  const submit = () => {
    setError("");
    if (!item || !session) return;

    if (formData.qty)
      placeOrder({
        ...formData,
        type: item.category,
        product: item.brand + " " + item.title,
        price: item.price * formData.qty,
        vaper: session?.name,
      }).then((res) => {
        console.log(res);
        if (res.success) {
          history.goBack();
          showSystemMessage(
            "Thanks a Bunch!",
            "Click here to view myLAB",
            "green",
            "/mylab"
          );
        } else showSystemMessage("Error!", res?.error, "red");
      });
    else {
      setError("Please fill in all fields");
    }
  };

  const calcHardware = (data: IFormData) => {
    var finalPrice = data.qty * (item?.price ?? 0);

    //points calculation

    var hasEnoughPoints = (session?.points ?? 0) >= finalPrice * 100;
    if (!hasEnoughPoints) data.usePoints = false;

    updateFormData({ ...data });
  };

  useEffect(() => {
    init();
  }, [init]);

  return loading ? (
    <PageLoader />
  ) : (
    <div id="orderContainer" className="fxDisplay fxStretch hardware">
      {item ? (
        <div
          id="contentContainer"
          className="fxDisplay fxStretch fxJustifyCenter"
        >
          <div
            className="fxDisplay fxFixed fxDirCol fxJustifyCenter"
            id="orderProductHead"
          >
            <span id="orderProductSubtitle">{item.brand}</span>
            <span id="orderProductTitle">{item.title}</span>
            <img
              id="orderProductImage"
              src={`../images/products/${item.category}/${getProductID(
                item
              )}.jpg`}
            />
          </div>

          <div
            className={`fxDisplay fxFixed fxDirCol fxJustifyCenter ${
              ordering ? "sending" : ""
            }`}
            id="orderProductSlip"
          >
            <table id="orderProductSlipTable">
              <tbody>
                <tr>
                  <td colSpan={2} id="orderProductSlipInfo">
                    <b>Quick Specs</b>
                    <p>{item.info}</p>
                  </td>
                </tr>

                {item.spec && (
                  <tr id="orderProductSlipRow_COLORS">
                    <td>Color</td>
                    <td id="orderProductSlip_COLOR">{item.spec}</td>
                  </tr>
                )}

                <tr>
                  <td>Quantity</td>
                  <td>
                    <select
                      value={formData.qty}
                      id="orderProductSlip_QTY"
                      onChange={(e) =>
                        calcHardware({
                          ...formData,
                          qty: Number(e.currentTarget.value),
                        })
                      }
                    >
                      <option value="1">1 Unit</option>
                      <option value="2">2 Units</option>
                      <option value="3">3 Units</option>
                      <option value="4">4 Units</option>
                      <option value="5">5 Units</option>
                      <option value="6">6 Units</option>
                      <option value="7">7 Units</option>
                      <option value="8">8 Units</option>
                      <option value="9">9 Units</option>
                      <option value="10">10 Units</option>
                    </select>
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <span className="credPopupError">{error}</span>
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <hr />
                  </td>
                </tr>
                <tr id="orderProductSlipPricing">
                  <td colSpan={2} style={{ textAlign: "right" }}>
                    {session && formData.usePoints ? (
                      <span id="orderProductSlipPrice">{(0).toFixed(2)}</span>
                    ) : (
                      <Fragment>
                        {item.oldPrice && (
                          <span id="orderProductSlipOldPrice">
                            {(item.oldPrice * formData.qty).toFixed(2)}
                          </span>
                        )}
                        <span id="orderProductSlipPrice">
                          {(item.price * formData.qty).toFixed(2)}
                        </span>
                        <br />
                        {item.oldPrice && (
                          <span id="orderProductSlipEarn">
                            This product is on{" "}
                            <b>
                              {Math.round(
                                100 - (item.price / item.oldPrice) * 100
                              )}
                              %
                            </b>{" "}
                            discount!
                          </span>
                        )}
                      </Fragment>
                    )}
                  </td>
                </tr>

                {session && (
                  <tr
                    id="orderProductSlipRedeem"
                    className={
                      (session.points ?? 0) >= item.price * formData.qty * 100
                        ? ""
                        : "notEnough"
                    }
                  >
                    <td colSpan={2} style={{ textAlign: "right" }}>
                      <div id="orderProductSlipRedeemBackground">
                        <input
                          style={{ display: "none" }}
                          id="orderProductSlipRedeemButton"
                          type="checkbox"
                          name="orderProductSlipRedeemButton"
                          onChange={(e) =>
                            calcHardware({
                              ...formData,
                              usePoints: Boolean(e.currentTarget.checked),
                            })
                          }
                          checked={formData.usePoints}
                        ></input>
                        <label
                          id="orderProductSlipRedeemButtonCaption"
                          htmlFor="orderProductSlipRedeemButton"
                        >
                          Get for Free!
                        </label>
                        {(session.points ?? 0) >=
                        item.price * formData.qty * 100 ? (
                          <span id="orderProductSlipPointsBalance">
                            <b>{`Use ${(
                              item.price *
                              formData.qty *
                              100
                            ).toFixed(0)} points!`}</b>
                            <br />
                            <i>{`You have ${session.points} in your account.`}</i>
                          </span>
                        ) : (
                          <span id="orderProductSlipPointsBalance">
                            <i>{`You have ${session.points} points in your account.`}</i>
                            <br />
                            <b>{`You need ${
                              item.price * formData.qty * 100 - session.points
                            } more.`}</b>
                          </span>
                        )}
                      </div>
                    </td>
                  </tr>
                )}

                <tr>
                  <td colSpan={2} style={{ textAlign: "right" }}>
                    <button
                      id="orderProductSlipExit"
                      type="button"
                      onClick={() => history.goBack()}
                    >
                      Back
                    </button>
                    <button
                      id="orderProductSlipPurchase"
                      type="button"
                      onClick={
                        session
                          ? () => submit()
                          : () => setCredentialPopup("login")
                      }
                    >
                      {session ? "Place Order" : "Login"}
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>

            <div id="orderProductSlipSending" style={{ visibility: "hidden" }}>
              <h4>sending slip</h4>
            </div>
          </div>
        </div>
      ) : (
        <div>item not found</div>
      )}
    </div>
  );
};

export default observer(HardwareDetail);
