import { action, makeObservable, observable, runInAction } from "mobx";
import agent from "../Helpers/agent";
import { IAccountDetails, IMyLabItem } from "../models/models";
import { RootStore } from "./rootStore";

export default class myAccountStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    makeObservable(this);

    this.rootStore = rootStore;
  }

  @observable myAcc?: IAccountDetails = undefined;
  @observable loading: boolean = false;
  @observable changeMarketingLoading: boolean = false;
  @observable changePassFormVisible: boolean = false;

  @action getData = async () => {
    if (
      this.rootStore.commonStore.session?.userid &&
      this.rootStore.commonStore.session?.seid
    ) {
      try {
        this.loading = true;

        const response = await agent.account.getMyAccount(
          this.rootStore.commonStore.session!.userid,
          this.rootStore.commonStore.session!.seid
        );

        if (response?.success === true) {
          runInAction(() => {
            this.myAcc = response.details;
          });
        } else if (response?.error) throw response?.error;
      } catch (f) {
        console.log(f);
      } finally {
        runInAction(() => {
          this.loading = false;
        });
      }
    }
  };

  @action changeMarketing = async (receiveEmails: boolean) => {
    try {
      console.info("setting MyAccount");
      this.changeMarketingLoading = true;

      const response = await agent.account.setMarketing(
        this.rootStore.commonStore.session!.email,
        receiveEmails
      );

      if (response.success === true) {
        runInAction(() => {
          this.myAcc!.marketing = response.value;
        });
      } else {
        console.log(response.error);
      }
    } catch (error) {
    } finally {
      this.changeMarketingLoading = false;
    }
  };

  @action dispose = () => {
    this.myAcc = undefined;
  };

  @action toggleChangePassPopup = (visible: boolean) => {
    this.changePassFormVisible = visible;
  };
}
