import { IHardwareItem } from "../models/models";

export function getProductID(item: IHardwareItem) {
  const result =
    (item.brand + item.title).replace(/[^a-z0-9]/gi, "") +
    (item.spec ? "_" + item.spec.replace(/[^a-z0-9]/gi, "") : "");

  return result.toLowerCase();
}

export function getFlavorID(flavor: string) {
  const result = flavor.replace(/[^a-z0-9]/gi, "");
  return result.toLowerCase();
}

export function formatDate(date: string) {
  const d = new Date(date);
  const r = d.getDate() + "/" + (d.getMonth() + 1) + "/" + d.getFullYear();
  return r;
}
