// import { configure } from "mobx";
import { createContext } from "react";
import CommonStore from "./commonStore";
import HardwareStore from "./hardwareStore";
import NectarStore from "./nectarStore";
import MyLabStore from "./myLabStore";
import MyAccountStore from "./myAccountStore";

// interface iSession {
//   name: string
// }

// configure({ enforceActions: "always" });

export class RootStore {
  commonStore: CommonStore;
  hardwareStore: HardwareStore;
  nectarStore: NectarStore;
  myLabStore: MyLabStore;
  myAccountStore: MyAccountStore;

  constructor() {
    this.commonStore = new CommonStore(this);
    this.hardwareStore = new HardwareStore(this);
    this.nectarStore = new NectarStore(this);
    this.myLabStore = new MyLabStore(this);
    this.myAccountStore = new MyAccountStore(this);
  }
}

export const RootStoreContext = createContext(new RootStore());
