import { observer } from "mobx-react-lite";
import React, {
  Fragment,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { RouteComponentProps } from "react-router";
import { RootStoreContext } from "../../stores/rootStore";
import { useHistory } from "react-router-dom";
import { INectarItem, INectarOrder } from "../../models/models";
import { getFlavorID } from "../../Helpers/helper";
import NicotineTypeHelp from "../NicotineTypeHelp";
import PageLoader from "../PageLoader";

interface RouteParams {
  id: string;
}

interface Iprops extends RouteComponentProps<RouteParams> {}

const NectarDetail: React.FC<Iprops> = ({ match }) => {
  const rootStore = useContext(RootStoreContext);
  const { session, setCredentialPopup, showSystemMessage } =
    rootStore.commonStore;

  const { getProduct, prices, ordering, placeOrder } = rootStore.nectarStore;

  let history = useHistory();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const [nicTypeHelp, setNicTypeHelp] = useState(false);

  const [item, setItem] = useState<INectarItem | undefined>();

  const [calculatedNectar, setCalculatedNectar] = useState<{
    price: number;
    hasEnoughPoints: boolean;
    earn: number;
  }>({
    price: 0,
    hasEnoughPoints: false,
    earn: 0,
  });

  const [formData, updateFormData] = React.useState<INectarOrder>({
    qty: 1,
    nicType: "F",
    vaper: "Your name on bottle",
    nozzle: false,
    usePoints: false,
  });

  const submit = () => {
    setError("");
    if (!item) return;

    if (
      formData.nicType &&
      formData.nicotine &&
      formData.qty &&
      formData.size &&
      formData.vg
    )
      placeOrder({
        ...formData,
        flavor: item.name,
      }).then((res) => {
        console.log(res);
        if (res.success) {
          history.goBack();

          showSystemMessage(
            "Thanks a Bunch!",
            "Click here to view myLAB",
            "green",
            "/mylab"
          );
        } else showSystemMessage("Error!", res?.error, "red");
      });
    else {
      setError("Please fill in all fields");
    }
  };

  const calcNectar = (data: INectarOrder) => {
    console.info("NectarDetail calcNectar()", data);

    if (data.size === undefined || data.nicotine === undefined) {
      updateFormData({ ...data });
      //console.info(data, calculatedNectar);

      return;
    }

    //if Salt - then max out at 120ml
    if (data.nicType === "S" && data.size > 120) {
      data.size = 120;
    }

    //if nicotine is Blank or Zero - switch to Freebase
    if (data.nicotine === 0) data.nicType = "F";

    //if size is 120ml or less, remove nozzle
    if (data.size <= 120) data.nozzle = false;

    var unitPrice = prices.filter(
      (p) => p.nicType === data.nicType && p.size === data.size
    )[0]?.price;

    //if Unflavoured - give 10% off on UnitPrice
    if (item!.name === "Unflavored") unitPrice = unitPrice * 0.9;

    var nozzlePrice = data.nozzle ? 2 : 0;

    var finalPrice = data.qty * (unitPrice + nozzlePrice);

    //points calculation

    var hasEnoughPoints = (session?.points ?? 0) >= finalPrice * 100;
    if (!hasEnoughPoints) data.usePoints = false;

    var earnPoints = 0;
    if (!data.usePoints)
      earnPoints = Math.floor((15 - data.nicotine) * (data.qty * unitPrice));

    updateFormData({ ...data });

    setCalculatedNectar({
      price: finalPrice,
      hasEnoughPoints: hasEnoughPoints,
      earn: earnPoints,
    });

    // console.info(data, {
    //   price: finalPrice,
    //   hasEnoughPoints: hasEnoughPoints,
    //   earn: earnPoints,
    // });
  };

  const init = useCallback(async () => {
    console.info("NectarDetail Init");

    setLoading(true);
    const res = await getProduct(match.params.id);
    setItem(res);

    setLoading(false);
    console.log(session?.name);
    calcNectar({
      ...formData,
      vaper: session?.name ?? formData.vaper,
    });
  }, [session]);

  // useEffect(() => {
  //   calcNectar(formData);
  // }, [session]);

  useEffect(() => {
    console.info("NectarDetail UseEffect");
    init();
    //.then(() => {
    //  calcNectar({
    //    ...formData,
    //    vaper: session?.name ?? formData.vaper,
    //   });
    //   console.info("item?.name", item?.name);
    // });
  }, [session]);

  return loading ? (
    <PageLoader loadingText="loading" componentText="nectar" />
  ) : (
    <div id="orderContainer" className="fxDisplay fxStretch">
      {item ? (
        <div
          id="contentContainer"
          className="fxDisplay fxStretch fxJustifyCenter"
        >
          <div
            className="fxDisplay fxFixed fxDirCol fxJustifyCenter"
            id="orderProductHead"
          >
            <span id="orderProductSubtitle">blend your very own </span>
            <span id="orderProductTitle">{item?.name}</span>

            <img
              id="orderProductImage"
              src={`../images/products/nectars/${getFlavorID(item.name)}.png`}
            />
            <span id="orderProductTags">{item?.props.join(", ")}</span>
            <span id="orderProductHeadInfo">{item.desc}</span>
          </div>

          <div
            className={`fxDisplay fxFixed fxDirCol fxJustifyCenter ${
              ordering ? "sending" : ""
            }`}
            id="orderProductSlip"
          >
            <table id="orderProductSlipTable">
              <tbody>
                <tr>
                  <td style={{ paddingTop: "15px" }}>Custom Text</td>
                  <td style={{ paddingTop: "15px" }}>
                    <input
                      type="text"
                      id="orderProductSlip_CUSTOMTEXT"
                      style={{ width: "100%" }}
                      maxLength={25}
                      // value={session ? session.name : "Your name on bottle"}
                      value={formData.vaper}
                      onChange={(e) =>
                        calcNectar({
                          ...formData,
                          vaper: e.target.value,
                        })
                      }
                    />
                  </td>
                </tr>

                <tr>
                  <td>Size (ml)</td>
                  <td>
                    <span
                      className={`orderProductSlip_SIZE ${
                        formData.size === 30 ? "selected" : ""
                      }`}
                      onClick={() => calcNectar({ ...formData, size: 30 })}
                    >
                      30
                    </span>
                    <span
                      className={`orderProductSlip_SIZE ${
                        formData.size === 60 ? "selected" : ""
                      }`}
                      onClick={() => calcNectar({ ...formData, size: 60 })}
                    >
                      60
                    </span>
                    <span
                      className={`orderProductSlip_SIZE ${
                        formData.size === 120 ? "selected" : ""
                      }`}
                      onClick={() => calcNectar({ ...formData, size: 120 })}
                    >
                      120
                    </span>
                    {formData.nicType === "F" && (
                      <Fragment>
                        <span
                          className={`orderProductSlip_SIZE ${
                            formData.size === 250 ? "selected" : ""
                          }`}
                          onClick={() => calcNectar({ ...formData, size: 250 })}
                        >
                          250
                        </span>
                        <span
                          className={`orderProductSlip_SIZE ${
                            formData.size === 500 ? "selected" : ""
                          }`}
                          onClick={() => calcNectar({ ...formData, size: 500 })}
                        >
                          500
                        </span>
                      </Fragment>
                    )}
                  </td>
                </tr>

                <tr>
                  <td>Nicotine</td>
                  <td>
                    <select
                      value={
                        formData.nicotine !== undefined ? formData.nicotine : ""
                      }
                      id="orderProductSlip_NICOTINE"
                      onChange={(e) =>
                        calcNectar({
                          ...formData,
                          nicotine: Number(e.currentTarget.value),
                        })
                      }
                    >
                      <option disabled value={""}>
                        -- select --
                      </option>
                      <option value={0}>Zero</option>
                      <option value={1}>1mg</option>
                      <option value={2}>2mg</option>
                      <option value={3}>3mg</option>
                      <option value={4}>4mg</option>
                      <option value={5}>5mg</option>
                      <option value={6}>6mg</option>
                      <option value={7}>7mg</option>
                      <option value={8}>8mg</option>
                      <option value={9}>9mg</option>
                      <option value={10}>10mg</option>
                      <option value={11}>11mg</option>
                      <option value={12}>12mg</option>
                      <option value={13}>13mg</option>
                      <option value={14}>14mg</option>
                      <option value={15}>15mg</option>
                    </select>
                  </td>
                </tr>
                {(formData.nicotine ?? 0) > 0 && (
                  <tr id="orderProductSlipRow_NICTYPE">
                    <td>Nicotine Type</td>
                    <td>
                      <span
                        className={`orderProductSlip_NICTYPE ${
                          formData.nicType === "F" ? "selected" : ""
                        }`}
                        onClick={() =>
                          calcNectar({ ...formData, nicType: "F" })
                        }
                      >
                        Freebase
                      </span>
                      <span
                        className={`orderProductSlip_NICTYPE ${
                          formData.nicType === "S" ? "selected" : ""
                        }`}
                        onClick={() =>
                          calcNectar({ ...formData, nicType: "S" })
                        }
                      >
                        Salt
                      </span>
                      <span
                        className="info"
                        onClick={() => setNicTypeHelp(true)}
                      >
                        ?
                      </span>
                    </td>
                  </tr>
                )}

                <tr>
                  <td>VG/PG Ratio</td>
                  <td>
                    <select
                      value={formData.vg !== undefined ? formData.vg : ""}
                      id="orderProductSlip_VG"
                      onChange={(e) =>
                        calcNectar({
                          ...formData,
                          vg: Number(e.currentTarget.value),
                        })
                      }
                    >
                      <option value={""} disabled>
                        -- select --
                      </option>
                      <option value="20">20/80</option>
                      <option value="40">40/60</option>
                      <option value="50">50/50</option>
                      <option value="60">60/40</option>
                      <option value="65">65/35</option>
                      <option value="70">70/30</option>
                      <option value="80">80/20</option>
                      <option value="100">MAX</option>
                    </select>
                  </td>
                </tr>
                {formData.size &&
                  (formData.size === 250 || formData.size === 500) && (
                    <tr id="orderProductSlipRow_NOZZLE">
                      <td>Bottle Cap</td>
                      <td>
                        <span
                          className={`orderProductSlip_NOZZLE ${
                            formData.nozzle !== true ? "selected" : ""
                          }`}
                          onClick={() =>
                            calcNectar({ ...formData, nozzle: false })
                          }
                        >
                          Standard
                        </span>
                        <span
                          className={`orderProductSlip_NOZZLE ${
                            formData.nozzle ? "selected" : ""
                          }`}
                          onClick={() =>
                            calcNectar({ ...formData, nozzle: true })
                          }
                        >
                          Nozzle (+2.00)
                        </span>
                      </td>
                    </tr>
                  )}

                <tr>
                  <td>Quantity</td>
                  <td>
                    <select
                      id="orderProductSlip_QTY"
                      defaultValue={1}
                      onChange={(e) =>
                        calcNectar({
                          ...formData,
                          qty: Number(e.currentTarget.value),
                        })
                      }
                    >
                      <option value="1">1 Bottle</option>
                      <option value="2">2 Bottles</option>
                      <option value="3">3 Bottles</option>
                      <option value="4">4 Bottles</option>
                      <option value="5">5 Bottles</option>
                      <option value="6">6 Bottles</option>
                      <option value="7">7 Bottles</option>
                      <option value="8">8 Bottles</option>
                    </select>
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <span className="credPopupError">{error}</span>
                  </td>
                </tr>

                <tr>
                  <td colSpan={2}>
                    <hr />
                  </td>
                </tr>
                {formData.nicotine !== undefined &&
                  formData.size !== undefined && (
                    <Fragment>
                      <tr id="orderProductSlipPricing">
                        <td colSpan={2} style={{ textAlign: "right" }}>
                          <span id="orderProductSlipPrice">
                            
                            {(session && formData.usePoints
                              ? 0
                              : calculatedNectar.price
                            ).toFixed(2)}
                          </span>
                          {session && calculatedNectar.earn > 0 && (
                            <span id="orderProductSlipEarn">
                              you'll earn <b> {calculatedNectar.earn}</b> points
                              with this purchase!
                            </span>
                          )}
                        </td>
                      </tr>

                      {session && (
                        <tr
                          id="orderProductSlipRedeem"
                          className={
                            calculatedNectar.hasEnoughPoints === true
                              ? ""
                              : "notEnough"
                          }
                        >
                          <td colSpan={2} style={{ textAlign: "right" }}>
                            <div id="orderProductSlipRedeemBackground">
                              <input
                                style={{ display: "none" }}
                                id="orderProductSlipRedeemButton"
                                type="checkbox"
                                name="orderProductSlipRedeemButton"
                                onChange={(e) =>
                                  calcNectar({
                                    ...formData,
                                    usePoints: Boolean(e.currentTarget.checked),
                                  })
                                }
                                checked={formData.usePoints}
                              ></input>
                              <label
                                id="orderProductSlipRedeemButtonCaption"
                                htmlFor="orderProductSlipRedeemButton"
                              >
                                Get for Free!
                              </label>
                              {calculatedNectar.hasEnoughPoints ? (
                                <span id="orderProductSlipPointsBalance">
                                  <b>{`Use ${(
                                    calculatedNectar.price * 100
                                  ).toFixed(0)} points!`}</b>
                                  <br />
                                  <i>{`You have ${session.points} in your account.`}</i>
                                </span>
                              ) : (
                                <span id="orderProductSlipPointsBalance">
                                  <i>{`You have ${session.points} points in your account.`}</i>
                                  <br />
                                  <b>{`You need ${
                                    calculatedNectar.price * 100 -
                                    session.points
                                  } more.`}</b>
                                </span>
                              )}
                            </div>
                          </td>
                        </tr>
                      )}

                      <tr>
                        <td colSpan={2} style={{ textAlign: "right" }}>
                          <button
                            id="orderProductSlipExit"
                            type="button"
                            onClick={() => history.goBack()}
                          >
                            Back
                          </button>
                          <button
                            id="orderProductSlipPurchase"
                            type="button"
                            onClick={
                              session
                                ? () => submit()
                                : () => setCredentialPopup("login")
                            }
                          >
                            {session ? "Place Order" : "Login"}
                          </button>
                        </td>
                      </tr>
                    </Fragment>
                  )}
              </tbody>
            </table>
            <div id="orderProductSlipSending" style={{ visibility: "hidden" }}>
              <h4>sending slip</h4>
            </div>
          </div>

          {nicTypeHelp && (
            <NicotineTypeHelp closeFunc={() => setNicTypeHelp(false)} />
          )}
        </div>
      ) : (
        <div>item not found</div>
      )}
    </div>
  );
};

export default observer(NectarDetail);
